<script>
import { ref, onMounted } from "vue"
import Header from "../../components/global/header/HeaderArea.vue"
import Footer from "../../components/global/footer/FooterArea.vue"
import { useRouter } from "vue-router"
import { Toast } from "../../components/global/swal.js"
import { focusInput } from "@/utils/fixFooter"
import { apiGetEmployer, apiPutEmployer, apiUpload } from "@/api/api"
import { useUserStore } from "@/store/global"

export default {
  setup() {
    const userStore = useUserStore()
    const router = useRouter()
    const profileData = ref({})

    //表單驗證
    const form = ref(null)
    const save = async (e) => {
      // console.log(profileData.value)
      e.preventDefault()
      if (form.value.reportValidity()) {
        // console.log(profileData.value)
        try {
          await apiPutEmployer(profileData.value)
          userStore.userName = undefined
          Toast("修改成功")
        } catch (error) {
          console.log(error)
        }
      }
    }

    onMounted(async () => {
      focusInput()
      // call api
      profileData.value = await apiGetEmployer()
      profileData.value = profileData.value.data
      // console.log(profileData.value)
    })

    //檔案上傳
    const myUploadFile = ref(null)
    const useIdCheckFileSize = async (id, requestData) => {
      //檢查上傳檔案大小
      const fileInput = document.getElementById(`formFile${id}`)
      const getFile = fileInput?.files?.[0]
      if (!getFile) return false

      const fileSize = getFile?.size / 1024 / 1024
      if (fileSize > process.env.VUE_APP_UPLOAD_LIMIT) {
        fileInput.value = ""
        Toast(`圖片檔案大小上限為${process.env.VUE_APP_UPLOAD_LIMIT}MB`)
        return false
      }
      const response = await apiUpload(requestData)
      if (response.status) {
        // 成功回拋 response
        return response
      } else {
        fileInput.value = ""
        const errorMessage = response.message || "上傳失敗"
        Toast(errorMessage)
        return false
      }
    }
        
    const handleFileUpload = async () => {
      try {
        //一定要這樣寫 [ref的名稱].value.files //固定寫files=FileList
        let requestData = new FormData()
        requestData.append("singleFile", myUploadFile.value.files[0])
        requestData.append("uploadType", "Avatar")
        // console.log(myUploadFile)
        //檢查上傳檔案大小
        // const fileInput = document.getElementById("formFile")
        // const getFile = fileInput?.files?.[0]
        // if (getFile) {
        //   const fileSize = getFile?.size / 1024 / 1024
        //   if (fileSize > process.env.VUE_APP_UPLOAD_LIMIT) {
        //     fileInput.value = ""
        //     Toast(`圖片檔案大小上限為${process.env.VUE_APP_UPLOAD_LIMIT}MB`)
        //   } else {
        //     const response = await apiUpload(requestData)
        //     if (response.status) {
        //       profileData.value.avatarUrl = response.data.url
        //       profileData.value.avatarImgId = response.data.id
        //     } else {
        //       fileInput.value = ""
        //       const errorMessage = response.message || "上傳失敗"
        //       Toast(errorMessage)
        //     }
        //   }
        // }

        const response = await useIdCheckFileSize("", requestData)
        if (!response) return

        profileData.value.avatarUrl = response.data.url
        profileData.value.avatarImgId = response.data.id
      } catch (error) {
        console.log("error", error)
      }
    }

    return { save, form, profileData, handleFileUpload, myUploadFile }
  },
  components: {
    Header,
    Footer,
  },
}
</script>

<template>
  <Header title="會員資料" backToPath="-1" />
  <div class="profile c-main">
    <div class="main">
      <div class="mb-3 avatar-container">
        <label class="form-file-label">
          <img
            :src="profileData.avatarUrl"
            v-if="profileData.avatarUrl"
            onerror="this.onerror=null; this.src='https://fakeimg.pl/91x91/'"
          />
          <img
            src="@/assets/image/noavatar.jpg"
            v-if="!profileData.avatarUrl"
          />
          <i class="fas fa-camera"></i>
          <input
            class="form-control"
            type="file"
            id="formFile"
            accept="image/png, image/jpeg"
            ref="myUploadFile"
            @change="handleFileUpload"
            style="display: none"
          />
        </label>
        <!-- <input type="file" class="form-file-input"> -->
      </div>
    </div>
    <div class="main1">
      <form ref="form">
        <div class="mb-3">
          <label class="form-label">姓名<span class="star">*</span></label>
          <input
            type="text"
            class="form-control"
            placeholder="請輸入姓名"
            v-model="profileData.name"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label">性別<span class="star">*</span></label>
          <div class="input-group">
            <div class="col form-check">
              <input
                type="radio"
                class="form-check-input"
                id="Male"
                value="Male"
                v-model="profileData.gender"
              />
              <label class="form-check-label" for="Male">男性</label>
            </div>
            <div class="col form-check">
              <input
                type="radio"
                class="form-check-input"
                id="Female"
                value="Female"
                v-model="profileData.gender"
              />
              <label class="form-check-label" for="Female">女性</label>
            </div>
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label">手機號碼</label>
          <input
            type="text"
            class="form-control"
            placeholder="請輸入手機號碼"
            v-model="profileData.phoneNumber"
            pattern="^09\d{2}?\d{3}?\d{3}$"
            title="請輸入手機號碼"
            readonly
          />
        </div>
        <div class="mb-3">
          <label class="form-label">電子信箱</label>
          <input
            type="email"
            class="form-control"
            placeholder="請輸入電子信箱"
            v-model="profileData.mail"
          />
        </div>
      </form>
      <div class="mt-4b btn-container">
        <div class="row">
          <div class="col">
            <button class="btn btn-outline-primary" type="button" @click="save">
              儲存資料
            </button>
          </div>
          <div class="col">
            <button
              class="btn btn-outline-primary"
              type="button"
              @click="$router.push({ path: '/member' })"
            >
              取消返回
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Footer />
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.profile {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
  padding: 1.25rem;
}
</style>
